<template>
  <div class="header">
    <a-row class="nav-row" type="flex" justify="space-between" align="middle">
      <!-- Logo Section -->
      <a-col :span="6" class="logo-container">
        <div class="logo">
          <img src="../assets/lablogo.svg" alt="TVDS Logo" class="logo-img" />
          <!-- <span class="logo-text">TVDS Lab</span> -->
        </div>
      </a-col>

      <!-- Menu Section -->
      <a-col :span="18" class="menu-container">
        <a-menu
          v-model="current"
          mode="horizontal"
          class="main-menu"
          @click="pushmenu"
        >
        <a-menu-item key="index">
            <a-icon type="home" />Home
          </a-menu-item>
          <a-menu-item key="publications">
            <a-icon type="profile" />Publications
          </a-menu-item>
          <a-menu-item key="team">
            <a-icon type="team" />Team
          </a-menu-item>
          <a-menu-item key="research">
            <a-icon type="search" />Vacancies
          </a-menu-item>
          <a-menu-item key="activities">
            <a-icon type="schedule" />Activities
          </a-menu-item>
        </a-menu>
      </a-col>
    </a-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      current: ["index"],
    };
  },
  watch: {
    $route(to) {
      this.current = [to.path.replace("/", "") || "index"];
    },
  },
  methods: {
    pushmenu(item) {
      if (this.$route.path !== "/" + item.key) {
        this.$router.push({ path: "/" + item.key });
      }
    },
  },
};
</script>

<style scoped>
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  z-index: 1000;
  padding: 0 40px;
}

.nav-row {
  height: 70px;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo-img {
  height: 90px;
}

.logo-text {
  font-size: 22px;
  font-weight: bold;
  color: #1f1f1f;
  font-family: 'Poppins', 'Helvetica Neue', sans-serif;
}

.menu-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.main-menu {
  border-bottom: none;
  font-weight: 500;
  font-size: 16px;
  font-family: 'Poppins', 'Helvetica Neue', sans-serif;
}

.main-menu .ant-menu-item-selected,
.main-menu .ant-menu-submenu-selected {
  color: #1890ff !important;
  border-bottom: 2px solid #1890ff;
}

.main-menu .ant-menu-item:hover,
.main-menu .ant-menu-submenu:hover {
  color: #1890ff;
}

@media (max-width: 768px) {
  .header {
    padding: 0 20px;
  }
  .logo-text {
    font-size: 18px;
  }
  .main-menu {
    font-size: 14px;
  }
}
</style>
